<template>
  <Navbar 
  :lang="lang" 
  @searchNote="search = $event" 
  @changeLang="changeLang"/>
  <Notes :lang="lang" :notes="filterNotes" @delNote="delNote" @editNote="editNote" />
  <AddButton @click="modal = true" />
  <Modal
  :lang="lang" 
   v-if="modal" 
   @closeModal="closeModal" 
   @addNote="addNote" 
  :edit="edit"
   :changedNote="changedNote"
   @changeNote="changeNote"
   />
   
</template>

<script>
let id = 1;
import Navbar from "@/components/Navbar.vue";
import Notes from "@/components/Notes.vue";
import AddButton from "@/components/AddButton.vue";
import Modal from "@/components/Modal.vue";
import Lang from "@/lang";
export default {
  components: {
    Navbar,
    Notes,
    AddButton,
    Modal,
  },
  data() {
    return {
      search:null,
      modal: false,
      edit: false,
      changedNote: {},
      notes: [
        {
          id: id++,
          title: "Vue",
          text: "Lorem ipsum dolor sit amet, consectetur adipisicing elit. Excepturi, non.",
          date: "07.05.2014",
        },
        {
          id: id++,
          title: "React",
          text: "Lorem ipsum dolor sit amet, consectetur adipisicing elit. Excepturi, non.",
          date: "06.11.2012",
        },
        {
          id: id++,
          title: "Angular",
          text: "Lorem ipsum dolor sit amet, consectetur adipisicing elit. Excepturi, non.",
          date: "01.01.2009",
        },
      ],
      lang:"ru",
      words:Lang
    };
  },
  provide(){
   return {
    words:this.words,
   };
  },
  methods: {
    changeLang(value){
      this.lang = localStorage.lang = value;
    },
    changeNote(newNote){
      this.notes.forEach(note=>{
        if (note.id == newNote.id) {
          note.title = newNote.title
          note.text = newNote.text
          note.date = newNote.date
          
        }
      })
      this.closeModal()
    },
    editNote(id) {
      this.edit = this.modal = true
      this.changedNote = this.notes.find((note) => note.id == id);
    },
    delNote(index) {
      this.notes.splice(index, 1);
    },
    closeModal() {
   this.edit =  this.modal = false;
    },
    addNote(note) {
      this.notes.push({
        id: this.notes.length + 1,
        title: note.title,
        text: note.text,
        date: new Date().toLocaleTimeString(),
      });
      this.closeModal();
    },
  },
  computed:{
    filterNotes(){
      return this.search ? this.notes.filter(note => note.title.toLowerCase().includes(this.search.toLowerCase())) : this.notes
    }
  },
  watch: {
    notes: {
      handler(newNote) {
        localStorage.list = JSON.stringify(newNote);
      },
      deep: true,
    },
  },

  created() {
    let localNotes = localStorage.list;
    if (localNotes) {
      this.notes = JSON.parse(localNotes);
    }
    localStorage.lang = this.lang || 'ru'
  },
};
</script>

