<template>
  <div class="modal" @click="$emit('closeModal')">
    <div class="modal__window" @click.stop>
      <h3 class="modal__title">{{edit ? words.titlewindowedit[lang] : words.titlewindow[lang]}}</h3>
      <div class="modal__inputs">
        <label>
          <span>Title</span>
          <input type="text" placeholder="Title" v-model="title" />
        </label>
        <label>
          <span>Content</span>
          <input type="text" placeholder="Content" v-model="text" />
        </label>
      </div>
      <div class="modal__buttons">
        <button class="modal__btn del" @click="$emit('closeModal')">
          {{words.closebtn[lang]}}
        </button>
        <button v-if="edit" class="modal__btn edit" @click="changeNote">{{words.editbtn[lang]}}</button>
        <button v-else class="modal__btn edit" @click="addNote">{{words.addbtn[lang]}}</button>
      </div>
    </div>
  </div>
</template>

<script>
export default {
  data() {
    return {
      title: null,
      text: null,
    };
  },
  methods: {
    addNote() {
     if (this.title != null && this.text != null) {
         let newNote = {
        title: this.title,
        text: this.text,
      };
      this.$emit('addNote', newNote)
     }
    },
    changeNote(){
      if (this.title != null && this.text != null) {
        let changeNote = {
        id:this.changedNote.id,
        title: this.title,
        text: this.text,
        date:new Date().toLocaleDateString()
      };
      this.$emit('changeNote', changeNote)
      }
    }
  },
  props: {
    edit:Boolean,
    changedNote:Object,
    lang:String,
  },
  inject:['words'],
  mounted(){
    if (this.edit) {
      this.title = this.changedNote.title
    this.text = this.changedNote.text
    }
  }
};
</script>

